<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<view class="bodyBg"></view>
		<view class="content">
			<view class="title">
				<view class="title_left">
					<view>名师名题</view> — 
					<view class="type">{{title}}</view>
				</view>
				<view class="title_right">
					<view class="text">{{doQuetion || allQuetion}}</view>题
					<view v-if="wrong!=1 && wrong!=2">/{{allQuetion}}题</view> 
				</view>
			</view>
			<view class="list">
				<view class="celles" v-for="(item,index) in data" :key="index" @click="toDetails(item)">
					<view class="titles">{{item.title}}</view>
					<view v-if="item.attr" class="sign">{{item.attr}}</view>
					<view class="progress">
						<view :class="{study:item.one_leve_collection_count>0||item.one_leve_wrong_count>0||item.one_leve_do_question_count>0}">{{item.one_leve_collection_count || item.one_leve_wrong_count || item.one_leve_do_question_count}}</view>
						<view class="" v-if="wrong!=1 && wrong!=2">/{{item.one_leve_collection_count || item.one_leve_wrong_count || item.one_leve_question_count}}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { famousBrushInfo } from '@/common/http.api.js';
	export default{
		data() {
			return {
				models:'1', //白天模式
				token:'',
				title:'',
				doQuetion:0,
				allQuetion:0,
				wrong:0,
				data:{}
			}
		},
		onLoad(option) {
			console.log(option.unit_it)
			this.wrong = option.wrong
			this.title = option.title
			this.doQuetion = option.do
			this.allQuetion = option.all
			this.models = store.state.model
			this.token = store.state.vuex_token
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#021734',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#6ab4ff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
			this.data = JSON.parse(decodeURIComponent(option.info))
			console.log(this.data)
		},
		methods: {
			open(e) {
			  // console.log('open', e)
			},
			close(e) {
			  // console.log('close', e)
			},
			change(e) {
			  // console.log('change', e)
			},
			// 去题目
			toDetails(e) {
				console.log(e)
				if(e.one_leve_question_count==0){
					this.$u.toast("题库暂无数据")
				}else{
					uni.navigateTo({
						url: '/pages/index/Details?unit_it='+e.id+'&wrong='+this.wrong+"&title="+e.title+"&sign="+e.attr+"&answerCard=1"+'&famous=1'
					})
				}
			}
		}
	}
</script>

<style lang="scss">
	.bodyBg{
		background-color: #6ab4ff;
		height: 100rpx;
		border-bottom-left-radius: 45%;
		border-bottom-right-radius: 45%;
	}
	.content{
		width: 90%;
		margin: 0 5%;
		margin-top: -6vh;
		min-height: 100vh;
		.title{
			padding: 40rpx 30rpx;
			background-color: #fff;
			border-radius: 20rpx;
			margin-bottom: 40rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.title_left{
				font-weight: 500;
				view{
					display: inline-block;
				}
				.type{
					color: #6ab4ff;
				}
			}
			.title_right{
				font-size: 28rpx;
				display: flex;
				.text{
					color: #6ab4ff;
				}
			}
		}
		.list{
			.celles{
				line-height: 88rpx;
				overflow: hidden;
				padding: 0 28rpx;
				.titles{
					width: 60%;
					float: left;
					margin-right: 50rpx;
					overflow-x: scroll;
					white-space: nowrap;
				}
				.sign{
					background-color: #6ab4ff;
					color: #fff;
					float: left;
					font-size: 20rpx;
					border-radius: 6rpx;
					line-height: 38rpx;
					width: 60rpx;
					height: 38rpx;
					text-align: center;
					margin-top: 28rpx;
				}
				.progress{
					display: flex;
					float: right;
					view{
						display: inline-block;
					}
					.study{
						color: #6ab4ff;
					}
				}
			}
		}
	}
	.body{
		min-height: 100vh;
		background-color: #f7f7fb;
		.content{
			.title{
				box-shadow: 0rpx 4rpx 4rpx 0rpx #b3b3b3;
			}
			.list{
				background-color: #fff;
				border-top-left-radius: 20rpx;
				border-top-right-radius: 20rpx;
				.celles{
					margin-bottom: 1rpx;
				}
			}
		}
	}
	.bodyNight{
		height: 100vh;
		background-color: #3b3b3b;
		.bodyBg{
			background-color: #021734;
		}
		.content{
			.title{
				background-color: #303030;
				color: #fff;
			}
			.list{
				background-color: #303030;
				border-top-left-radius: 20rpx;
				border-top-right-radius: 20rpx;
				.celles{
					color: #fff;
					margin-bottom: 1rpx;
				}
			}
		}
	}
</style>